<template>
  <div>
    <button @click="loadExcelData"></button>
    <input
      type="file"
      ref="fileInput"
      accept=".xlsx, .xls"
      @change="handleFileChange"
      style="display: none"
    />
    <LoadingCompo v-if="isLoading"></LoadingCompo>
  </div>
</template>

<script setup>
import {ref} from 'vue' 
import { useRouter } from 'vue-router';
import { useStore } from '@/store/vueStore';

import sheetApi from '@/api/getExcelData.js'

import LoadingCompo from '@/components/LoadingCompo.vue';


const fileInput = ref(null);
const isLoading = ref(false);

const chartStore = useStore();

const router = useRouter();

const loadExcelData = () => { 
  fileInput.value.click();
}

const handleFileChange = async (e) => {

  const file = e.target.files[0];

  if (file) {

    isLoading.value = true;

    try {
      await new Promise((resolve) => setTimeout(resolve, 1000));
      const sheetData = await sheetApi.readExcelFile(file, 0);

      console.log(sheetData);
      
      chartStore.setChartData(sheetData)

      router.push('/result')
      
    } catch (err) {
      console.log("     ", err)
    } finally {
      isLoading.value = false;
    }
  }
}
</script>
<style scoped></style>
