import * as XLSX from 'xlsx'

const readExcelFile = (file, sheetIdx = 0) => {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = (e) => {
            const data = new Uint8Array(e.target.result);
            const resultData = XLSX.read(data, { type: 'array' })

            //    idx 
            const sheetName = resultData.SheetNames[sheetIdx];
            const workSheet = resultData.Sheets[sheetName];

            const sheetToJson = XLSX.utils.sheet_to_json(workSheet, { header: 1 })

            resolve(sheetToJson);
        }

        reader.onerror = (err) => reject(err);
        reader.readAsArrayBuffer(file)
    })

}

export default {
    readExcelFile,

}